import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { SortDescriptor, GroupDescriptor } from '@progress/kendo-data-query';
import { GridComponent } from 'src/app/shared/component/grid/grid.component';
import { WindowComponent } from '@progress/kendo-angular-dialog';
import { ConfirmComponent } from 'src/app/shared/component/dialogs/confirm/confirm.component';
import icMoreHoriz from '@iconify/icons-ic/twotone-more-horiz';
import icicActivated from '@iconify/icons-ic/twotone-check-box';
import icicUnActivated from '@iconify/icons-ic/twotone-check-box-outline-blank';
import icicdone from '@iconify/icons-ic/twotone-done';
import icClose from '@iconify/icons-ic/twotone-close';
import { AuthService } from 'src/app/core/services/auth.service';
import { MatDialog } from '@angular/material';
import { NotificationService } from 'src/app/core/services/notification.service';
import { ApiService } from 'src/app/core/services/api.service';
import { DocumentType } from 'src/app/shared/enum/document-type.enum';
import { SecurityOperation } from 'src/app/shared/enum/security-operation.enum';
import { CommonService } from 'src/app/core/utilities/common.helper';
import { SystemMessage } from 'src/app/shared/configdata/system-message';
import { LoadingComponent } from 'src/app/shared/component/dialogs/loading/loading.component';

@Component({
  selector: 'app-dich-vu-ky-thuat-benh-vien-list',
  templateUrl: './dich-vu-ky-thuat-benh-vien-list.component.html',
  styleUrls: ['./dich-vu-ky-thuat-benh-vien-list.component.scss']
})
export class DichVuKyThuatBenhVienListComponent implements OnInit {

  sort: SortDescriptor[] = [{
    field: 'TenNhomDichVuBenhVien',
    dir: 'asc'
  }];
  groups: GroupDescriptor[] = [{ field: 'LoaiGia' }];
  groupsParent: GroupDescriptor[] = [{ field: 'TenNhomDichVuBenhVien' }];
  expression: boolean = false;
  icMoreHoriz = icMoreHoriz;
  icActivated = icicActivated;
  icicUnActivated = icicUnActivated;
  icicdone = icicdone;
  icclose = icClose

  documentType: DocumentType;
  gridColumns: any[] = [];
  gridChildColumns: any[] = [];
  gridChildColumns2: any[] = [];
  urlGetDataGridChild: string = "DichVuKyThuatBenhVien/GetDataForGridChildAsync";
  urlGetTotalPageGridChild: string = "DichVuKyThuatBenhVien/GetTotalPageForGridChildAsync";
  urlGetDataGridChild2: string = "DichVuKyThuatBenhVien/GetDataForGridChildGiaBenhVienAsync";
  urlGetTotalPageGridChild2: string = "DichVuKyThuatBenhVien/GetTotalPageForGridGiaBenhVienAsync";
  @ViewChild(GridComponent, { static: true }) gridChild: GridComponent;
  @ViewChild(WindowComponent, { static: false }) windowChild: WindowComponent;
  @ViewChild('groupTemplate', { static: true }) groupTemplate: TemplateRef<any>;
  // @ViewChild('hieuLucTemplate', { static: true }) hieuLucTemplate: TemplateRef<any>;
  @ViewChild('gridParent', { static: false }) gridParent: GridComponent;

  constructor(private authService: AuthService,
    private dialog: MatDialog,
    private notificationService: NotificationService,
    private apiService: ApiService) { }

  ngOnInit() {
    //this.documentType = DocumentType.DanhMucDichVuKyThuat;
    this.documentType = DocumentType.DanhMucDichVuKyThuatTaiBenhVien;
    this.gridColumns = [
      { Field: "Ma", Title: "M", Width: 120, Sortable: true,LinkDetail:true},
      { Field: "Ma4350", Title: "M 4350", Width: 120, Sortable: true },
      { Field: "Ten", Title: "Tn", MinWidth: 170, Sortable: true },
      { Field: "TenNoiThucHien", Title: "Ni thc hin", Width: 200, Sortable: true },
      { Field: "NgayBatDauHienThi", Title: "Ngy bt u", Width: 110, Sortable: true },
      { Field: "ThongTu", Title: "Thng T", Width: 150, Sortable: true },
      { Field: "NghiDinh", Title: "Ngh nh", Width: 150, Sortable: true },
      { Field: "NoiBanHanh", Title: "Ni ban hnh", Width: 150, Sortable: true },
      { Field: "SoMayTT", Title: "S my TT", Width: 150, Sortable: true },
      { Field: "SoMayCBCM", Title: "S CBCM", Width: 150, Sortable: true },
      { Field: "ThoiGianThucHien", Title: "Thi gian thc hin", Width: 170, Sortable: true },
      { Field: "SoCaCP", Title: "S Ca CP", Width: 150, Sortable: true },
      { Field: "MoTa", Title: "M t", Width: 250, Sortable: true },
      { Field: "HieuLucHienThi", Title: "Hiu lc", Width: 100, Sortable: true },
      {
        Field: 'TenNhomDichVuBenhVien', Title: 'Nhm', Width: 150,Hidden:true, Sortable: true,TemplateGroupHeader: this.groupTemplate
      },
      { Field: "LoaiPhauThuatThuThuat", Title: "Loi PTTT", Width: 150, Sortable: true },
      { Field: "LoaiMauXetNghiemText", Title: "Loi mu xt nghim", MinWidth: 170, Sortable: true },
      
    ];

    this.gridChildColumns = [
      { Field: "GiaHienThi", Title: "Gi", Width: 50, Sortable: true, LinkDetail: false },
      { Field: "TiLeThanhToan", Title: "TLTT(%)", Width: 50, Sortable: true },
      { Field: "TuNgayDisplay", Title: "T ngy", Width: 100, Sortable: true, Dir: 'desc', DefaultSort: true  },
      { Field: "DenNgayDisplay", Title: "n ngy", Width: 100, Sortable: true }
    ];

    this.gridChildColumns2 = [
      { Field: "LoaiGia", Title: "Loi Gi", Width: 217, Sortable: true },
      { Field: "GiaHienThi", Title: "Gi", Width: 260, Sortable: true, LinkDetail: false },
     
      { Field: "TuNgayDisplay", Title: "T ngy", Width: 500, Sortable: true, Dir: 'desc', DefaultSort: true  },
      { Field: "DenNgayDisplay", Title: "n ngy", Width: 500, Sortable: true }
    ];
  }

  exportExcel() {
    const self = this;
    if (self.authService.hasPermission(self.documentType, SecurityOperation.Process)) {
      self.showPopupLoadingData();
      self.apiService.postExportExcel<string>('DichVuKyThuatbenhVien/ExportDichVuKyThuatbenhVien',
        self.gridParent._gridQueryInfo).subscribe(
          resultData => {
            const dateTimeNow = new Date();
            CommonService.downLoadFile(resultData, 'application/vnd.ms-excel', 'DichVuKyThuatbenhVien' + dateTimeNow.getFullYear() + '.xlsx');
            self.closePopupLoadingData();
          },
          (err: any) => {
            self.notificationService.showError(err.Message);
            self.closePopupLoadingData();
          });
    } else {
      self.notificationService.showError(SystemMessage.UnAuthorizedMessage);
    }
  }

  showPopupLoadingData() {
    this.dialog.open(LoadingComponent, {
      disableClose: true,
      width: '200px',
      height: '90px',
      data: { Title: 'ang xut excel...' }
    });
  }

  closePopupLoadingData() {
    this.dialog.closeAll();
  }
}
